*{
    margin:0%;
    padding: 0%;
    box-sizing: border-box;
    background-image: url(https://static.tumblr.com/c255078556660ae90624c25ab4ae23d8/kfrqbv6/qHyosysxv/tumblr_static_28fzonvjzyf408k4g84oo04k4_2048_v2.png);
}
.icecreamHeading{
    margin-top: 1rem;
    font-size: 30px;
    
}
.container{
    text-align: center;
    margin-top:2rem;
}

.classImg{
    width:100px;
    height: 100px;
    height: 100px;
    margin-right: 1rem;
    animation: all 1s linear;
    cursor: pointer;
}
.classImgBorder{
    border: 2px solid rgb(255,192,203);
    border-radius:8px;
}
.icecreamDesc{
    width:300px;
    display: inline-block;
    box-shadow: 0 5px 10px rgba(199,21,133);
    padding: 1.2rem 2rem;
    box-sizing: border-box;
}
.icecreamPrice{
    display: flex;
    justify-content: space-between;
    padding: 2rem auto;
margin-top: 2rem;
}
.priceTag{
    background-color: rgb(255,182,193);
    padding: 0.2rem;
    border-radius: 40px;
}
.rateTag{
    padding: 0.5rem;
    border-radius: 50%;
    box-shadow: 2px 5px 10px rgba(199,21,133);
}